import React, { Component } from 'react';
import { Navbar, Nav } from 'react-bootstrap';
import Loader from './Loader';
class Header extends Component {
    state = {
        menu: [
            { listname: '', listlink: '#me', target: '' },
            { listname: ' ', listlink: '#story', target: '' },
            { listname: ' ', listlink: '#certificate', target: '' },
            { listname: ' ', listlink: '#project', target: '' },
            { listname: ' ', listlink: 'https://amirreza-heydari.clarotm.ir/blog/', target: '_blank' },
        ]
    }
    render() {
        return (
            <>
                <Navbar bg="white" variant="white" className='border-1 border-bottom border-dark nav-shadow glass' sticky="top">
                    {/* <Container> */}
                    
                    <Navbar.Brand> </Navbar.Brand>
                    <Nav className="ms-md-5 ms-sm-0">
                        
                        {this.state.menu.map(item => <Loader width={"80"} height={"40"} class={"mx-2"}><Nav.Link className="mx-md-2 mx-sm-0 text-dark" target={item.target} href={item.listlink}>{item.listname}</Nav.Link></Loader>)}
                    </Nav>
                    
                    {/* </Container> */}

                </Navbar>
            </>
        );
    }
}

export default Header;